import { AfterViewInit, Component, Input, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import * as $ from 'jquery';
import { getUserInfo } from '../user-page/userpage.service';
import { UserInfo } from '../user/userInfo.module';
import { AppState } from '../store/app.reducer';
import { Store } from '@ngrx/store';
import * as fromAuthAction from  './store/auth.actions';

import {
  MatSnackBar,
  MatSnackBarHorizontalPosition,
  MatSnackBarVerticalPosition,
} from '@angular/material/snack-bar';
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit , AfterViewInit{
  @ViewChild('authForm',{static:false}) sLform:NgForm;
  @ViewChild('login',{static:false}) login:NgForm;
  @Input() logSwitchS;
  signIn:boolean = true;
  isLoading=false;
    err=null;
    subscribe:Subscription;
    loginInfo:string = "login"
    horizontalPosition: MatSnackBarHorizontalPosition = 'center';
    verticalPosition: MatSnackBarVerticalPosition = 'top';
    constructor(
      private _snackBar: MatSnackBar,
    private getUserPage:getUserInfo,
    private store:Store<AppState>) { }
  
    
  ngOnInit(): void {


    
  }
  ngAfterViewInit(){
    $(".show-login-block").click(function(){
      $(".sign").slideUp(1000);
      $(".login").slideDown(1000);
      
  
    })
  
    $(".show-sign-block").click(function(){
      $(".login").slideUp(1000);
      $(".sign").slideDown(1000);
    });
  }
 
  onSignUp(form:NgForm){


    
if(!form.valid){
  return
}
const email:string = form.value.email;
const password = form.value.password;
const Username:string = form.value.username;
const firstName:string = form.value.firstName;
const lastName:string = form.value.lastName;
const userI:UserInfo={Username,firstName,lastName,email}
this.getUserPage.userI.next(userI)


this.store.dispatch(new fromAuthAction.signUpStart({email:email,password:password}))





form.reset();
}


onLoginForm(form:NgForm){
this.store.dispatch(new fromAuthAction.LoginStart({email:form.value.email,password:form.value.password}));
 this.subscribe= this.store.select('Auth').subscribe(authState=>{
  if(authState.ErrorMessage&&authState.ErrorMessage!=null){
    this._snackBar.open(authState.ErrorMessage, 'x', {
      horizontalPosition: this.horizontalPosition,
      verticalPosition: this.verticalPosition,  })
  }else{

    this._snackBar.open('login success', 'x', {
      horizontalPosition: this.horizontalPosition,
      verticalPosition: this.verticalPosition,  })
  }
})
let dimissTime = setTimeout(()=>{
  this._snackBar.dismiss()
},2000)
    }
  



ngOnDestroy(): void {
this.subscribe.unsubscribe()
  
}
  }



